.Header {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  height: 3rem;
  align-items: center;

}

#back {
  background-color: #282727;

}

#div2 {
  display: flex;
  flex-direction: row;
  height: 100px;
  justify-content: space-between;

}

#div2 p {
  font-family: 'Bodoni Moda', serif;
  display: flex;
  color: rgb(14, 185, 253);
  font-size: 3rem;

}

#div2 h5 {
  font-family: 'Bodoni Moda', serif;
  color: rgb(14, 185, 253);
  font-size: 2.5rem;
  align-self: center;

}


NavLink .active {
  color: red;

}

.nav {
  color: rgba(246, 247, 249, 0.547);
  font-family: 'Bodoni Moda', serif;
  font-size: 1rem;

}

select {

  background-color: rgb(144, 144, 166);
}

@media (max-width: 900px) {
  #div2 p {
    font-size: 2.0rem;
  }
}

@media (max-width: 500px) {
  #div2 p {
    font-size: 1.5rem;
  }
}

@media (max-width: 1050px) {
  .Header {
    overflow-y: scroll;
    height: 50px;
  }
}